.hero-banner {
    background-color: $lightGreen;

    .hero-wrap {
        @include relate-aspect-ratio;
        @include media-breakpoint-down(sm) {
            iframe {
                min-height: 300%;
                min-width: 300%;
            }
        }

        .hero-background {
            width: 100%;
            // height: 100%;
            position: relative;
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center;

            position: relative;
            @include aspect-ratio(1440, 560);
            @include media-breakpoint-down(sm) {
                @include aspect-ratio(375, 560);

                iframe {
                    min-height: 300%;
                    min-width: 300%;
                }
            }
        }


        .hero-text {
            height: 100%;
            width: 100%;
            padding: 2rem;
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            z-index: 1;
            color: $white;

            .hero-title, .hero-subtitle {
                text-align: center;
                line-height: normal;
            }

            .hero-title {
                margin-bottom: -1rem;
                font-size: calc(2.13rem + 2.5vw);


                @include media-breakpoint-down(sm) {
                    margin-bottom: -0.5rem;
                    font-size: calc(1.65rem + 2vw);
                }
            }

            .hero-subtitle {
                font-family: $font-family-cbyg;
                margin-bottom: calc(0.1rem + 5vw);
                font-size: calc(0.9rem + 2.5vw);

                @include media-breakpoint-down(sm) {
                    font-size: calc(1.5rem + 2vw);
                    letter-spacing: 0.1px;
                    word-spacing: 3px;
                }
            }

            .hero-btn {
                position: relative;

                @include media-breakpoint-down(sm) {
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    right: 0;
                    text-align: center;
                    margin-bottom: calc(3rem + 6vw);
                }

                .btn {
                    background-color: $body-bg;
                    border-color: $body-bg;
                    color: $body-color;
                    text-transform: uppercase;
                    max-width: calc(7.6rem + 10vw);
                    min-width: calc(7.6rem + 10vw);
                    font-size: calc(.4rem + .45vw);
                    border-radius: 50px;

                    @include media-breakpoint-down(sm) {
                        max-width: calc(14.3rem + 10vw);
                        min-width: calc(14.3rem + 10vw);
                        font-size: calc(0.7rem + .5vw);
                    }

                    &:hover, &:active {
                        background-color: $body-color;
                        border-color: $body-color;
                        color: $body-bg;
                    }
                }
            }
        }


        .hero-play-btn {

            height: 100%;
            width: 100%;
            padding: 2rem;
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            z-index: 1;
            color: $white;
            z-index: 3;

            button {
                width: 5rem;
                height: 5rem;
                z-index: 3;

                img {
                    width: 30%;
                    height: 30%;
                    left: 3px;
                    position: relative;
                }

                &:hover {
                    background-color: $body-color;
                        border-color: $body-color;
                }
            }
        }
    }
}


