.alert {
  padding: 1.25rem;
  border-radius: 0;
  border: none;
  z-index: 1051!important;



  @include media-breakpoint-up(sm) {
    padding: 0.5625rem 1.25rem;
  }

  &-container {
    max-width: 900px;
    margin: 0 auto;
    text-align: center;

    >.col {
      .iphone & {
        @include media-max(576px) {
          display: inline-block !important;
        }
      }
    }

    @include media-breakpoint-up(sm) {
      text-align: left;
      padding: 0.5625rem 1.25rem;
    }

    img {
      width: 100%;
      height: 100%;
      max-width: 40px;
      max-height: 40px;
    }
  }

  &-text {
    color: $body-color;
    font-size: 0.9375rem;
    font-weight: 300;
    margin: 1rem 0;
    letter-spacing: 0.5px;

    @include media-breakpoint-up(lg) {
      font-size: 1.25rem;
      min-width: 145px;
    }

    @include media-breakpoint-up(sm) {
      margin: 0 2.75rem;
    }

    .iphone & {
      @include media-max(576px) {
        display: block !important;
        width: 100%;
      }
    }
  }

  .btn.btn-sm {
    max-width: 127px;
    width: 100%;
  }
}



